<template>
  <el-dialog
    :title="isOutputXml ? '' : ''"
    :visible.sync="centerDialogVisible"
    width="70%;"
    center
    @close="close"
  >
    <el-upload
      class="upload-box"
      drag
      accept=".xml"
      action
      :file-list="uploadFiles"
      :on-change="fileChange"
      :auto-upload="false"
      :limit="1"
      :on-exceed="exceedFile"
      v-if="!isOutputXml"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        
        <em></em>
        <div slot="tip" class="el-upload__tip">
          
          <em>xml</em>  <em>1</em> 
        </div>
      </div>
    </el-upload>
    <!--codemirror-->
    <div style="display: flex">
      <codemirror
        :value="currentNodeData"
        v-model="currentNodeData"
        :options="options"
        :style="{ width: isOutputXml ? '50%' : '100%' }"
        class="codemirror"
      ></codemirror>
      <!-- xmljson -->
      <json-viewer
        v-if="isOutputXml"
        class="xmlToJson"
        :value="xml2json"
        :expand-depth="5"
        copyable
        sort
      ></json-viewer>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="cancelUpload"> </el-button>
      <el-button type="primary" @click="confirmUpload" v-if="!isOutputXml"
        ></el-button
      >
      <el-button type="primary" @click="saveXmlFile" v-if="isOutputXml"
        >XML</el-button
      >
    </span>
  </el-dialog>
</template>
<script>
import 'codemirror/lib/codemirror.css'
import { codemirror } from 'vue-codemirror'
//  
// import 'codemirror/theme/mdn-like.css'
import 'codemirror/theme/lesser-dark.css'
import 'codemirror/mode/markdown/markdown.js'
// 
import "codemirror/mode/javascript/javascript.js"
import "codemirror/mode/properties/properties.js"
import 'codemirror/addon/scroll/annotatescrollbar.js'
import 'codemirror/addon/search/matchesonscrollbar.js'
import 'codemirror/addon/search/match-highlighter.js'
import 'codemirror/addon/search/jump-to-line.js'
import 'codemirror/addon/selection/active-line' //styleActiveLinetrue

import 'codemirror/addon/dialog/dialog.js'
import 'codemirror/addon/dialog/dialog.css'
import 'codemirror/addon/search/searchcursor.js'
import 'codemirror/addon/search/search.js'
// 
import 'codemirror/addon/fold/foldgutter.css'
import 'codemirror/addon/fold/foldcode'
import 'codemirror/addon/fold/foldgutter'
import 'codemirror/addon/fold/brace-fold'
import 'codemirror/addon/fold/comment-fold'
import X2JS from 'x2js'

export default {
  props: ['graphXml', 'isOutputXml'],
  data () {
    return {
      xml2json: '',
      uploadFiles: [],
      centerDialogVisible: false,
      currentNodeData: "",
      // codemirror
      options: {
        tabSize: 4,
        lineNumbers: true,
        line: true,
        mode: 'text/x-markdown',
        theme: 'lesser-dark',
        foldGutter: true,
        lineWrapping: true,
        styleActiveLine: true,
        autoRefresh: true,
        lint: true,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter', 'CodeMirror-lint-markers'],
      }
    }
  },
  components: {
    codemirror
  },
  watch: {
    currentNodeData: {
      handler (newvalue) {
        // let xmljson = x2js.xml2js(newvalue) //xml2jsxmljson
        //x2js
        var x2js = new X2JS();
        this.xml2json = x2js.xml2js(newvalue);
        // this.currentNodeData = newvalue
      },
      immediate: true,
      deep: true
    }
  },
  mounted () {
    this.centerDialogVisible = true
    this.currentNodeData = this.graphXml
  },
  methods: {

    close () {
      this.centerDialogVisible = false
      this.$parent.uploadDataVisible = false;
    },
    saveXmlFile () {
      var datastr = "data:text/json;charset=utf-8," + encodeURIComponent(this.currentNodeData);
      var downloadAnchorNode = document.createElement('a')
      downloadAnchorNode.setAttribute("href", datastr);
      downloadAnchorNode.setAttribute("download", 'data.xml')
      downloadAnchorNode.click();
      downloadAnchorNode.remove();
      this.$message.success(",...")
    },
    confirmUpload () {
      // 
      this.$emit("uploadPaintFlow", this.currentNodeData)
      this.centerDialogVisible = false
      this.$parent.uploadDataVisible = false;
    },
    cancelUpload () {
      console.log(this.currentNodeData)
      this.centerDialogVisible = false
      this.$parent.uploadDataVisible = false;
      this.currentNodeData = ""
    },

    tirggerFile () {

    },
    fileChange (file) {
      // 
      var fileType = file.name.split(".").pop()
      if (fileType !== 'xml') {
        this.$message.error(",XML")
        this.uploadFiles = []
        return
      }
      if (file) {
        let reader = new FileReader()
        reader.readAsText(file.raw) //utf-8
        reader.onload = () => {
          this.$message.success("")
          var result = reader.result;
          this.currentNodeData = result
        }
      }
    },
    exceedFile () {
      this.$message.error(",")
    }
  },
}
</script>
<style lang="less">
.upload-box {
  .el-upload {
    display: block;
    width: 360px;
    margin: 0 auto;
  }
}
.codemirror {
  margin-top: 40px;
  border-style: solid;
  border-color: #578dbf;
  border-width: 2px 2px 2px 0;
  position: relative;
  height: 500px;
}
.CodeMirror {
  height: 100%;
}
.codemirror::before {
  position: absolute;
  font-size: 17px;
  top: -30px;
  left: 10px;
  content: " :";
}
.xmlToJson {
  width: 50%;
  height: 100%;
}
.xmlToJson::before {
  position: absolute;
  font-size: 17px;
  top: 0px;
  left: 10px;
  content: "XMLJSON :";
}
</style>